import React, { useState, useEffect }  from "react";
import "../css/listings.css"
import Card from "./reusable/card";
import { paginate } from '../services/paginationService';
import Pagination from '../components/reusable/pagination';


function Listings() {
    const [allData, setAllData] = useState([]);
    const [pageSize] = useState(4);
    const [currentPage, setCurrentPage] = useState(1);
    const [posts, setPosts] = useState(paginate(allData, currentPage, pageSize));

    const fetchData = async function () {
        const _listings = await fetch("/getListings")
        .then(res => res.json());
        // console.log(_listings)
        setAllData(_listings);
    }
    
    // useEffect(() => {
    //     fetchData();
    // }, []);

    // useEffect(() => {
    //     setPosts(paginate(allData, currentPage, pageSize));
    // }, [allData, currentPage, pageSize]);


    const handlePageChange = (pageNumber) => {
        setCurrentPage(pageNumber);
    }
    
    

    

	return (
        <>
            <div class="container listings p-0 mt-4">
                <h1 class="main-heading text-center pb-2">Find Your Perfect Home</h1>
                {/* card div */}
                {posts.map (listing => 
                    <div class="col-6 p-2 d-inline-block">
                        <Card element={listing} />
                    </div>     
                )}
                <Pagination 
                    itemsCount={allData.length}
                    pageSize={pageSize}
                    onPageChange={handlePageChange}
                    currentPage={currentPage}
                />
                
            </div>
        </>
	);
}
export default Listings;
