import React, { useState } from 'react';
import _ from 'lodash';

export default function Pagination(props) {

    const {itemsCount, pageSize, onPageChange, currentPage} = props;
    // console.log("Props", props);
    const pagesCount = Math.ceil(itemsCount / pageSize);
    const [pages] = useState(_.range(1, pagesCount + 1));

    if (pagesCount <= 1) return null;
    
    return (
        // <div>
        //     <nav>
        //         <ul class="pagination">
        //             {
        //                 pages.map((page, index) => (
        //                     <li 
        //                         class={page === currentPage ? "page-item active" : "page-item"} 
        //                         key={page}
        //                         tabIndex={0}
        //                         >
        //                         <a class="page-link" onClick={() => {onPageChange(page)}}>
        //                             {page}
        //                         </a>
        //                     </li>
        //                 ))
        //             }
        //         </ul>
        //     </nav>
        // </div>
        <nav aria-label="Page Navigation" expand="xs">
        <ul class="pagination">
          <li class="page-item">
            <a class="page-link" onClick={() => {onPageChange(currentPage - 1)}} aria-label="Previous">
              <span aria-hidden="true">&laquo;</span>
              <span class="sr-only">Previous</span>
            </a>
          </li>
          {
                pages.map((page, index) => (
                    <li 
                        class="page-item"
                        // class={page === currentPage ? "page-item active" : "page-item"} 
                        key={page}
                        tabIndex={0}
                        >
                        <a class="page-link" onClick={() => {onPageChange(page)}}>
                            {page}
                        </a>
                    </li>
                ))
            }
          <li class="page-item">
            <a class="page-link"  onClick={() => {onPageChange(currentPage - 1)}} aria-label="Next">
              <span aria-hidden="true">&raquo;</span>
              <span class="sr-only">Next</span>
            </a>
          </li>
        </ul>
      </nav>
    );
}